.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    width: 150px;
    height: 150px;
    margin: 0 20px;
  }
}

.App-header {
  background-color: #282c34;
  //background-color: #FFF;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  a{
    cursor: pointer;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#view-container{
  min-height: 70vh;
  background: #FFF;
  padding: 20px;
}

.copy-to-clipboard{
  border-bottom: 1px dashed gray;
  cursor: pointer;
}

/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dottedrgba(0,0,0,0.5); /* If you want dots under the hoverable text */
  .tooltiptext {
    visibility: hidden;
    width: 120px;
    bottom: 100%;
    left: 50%;
    margin-left: -60px;
    background-color: rgba(0,0,0,0.5);
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
   
    /* Position the tooltip text - see examples below! */
    position: absolute;
    z-index: 1;
  }
  .tooltiptext::after {
    content: " ";
    position: absolute;
    top: 100%; /* At the bottom of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: rgba(0,0,0,0.5) transparent transparent transparent;
  }
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.switch{
  display: flex;
  justify-content: center;
  align-items: center;
  > div {
    display: inline;
  }
}