/// The "primary" color suffix numbers that are available for use. This is
/// really just created for the `rmd-theme-get-swatch` mixin, but might be
/// useful since it also gets added to the scssVariables export.
/// @type List
$rmd-theme-primary-suffixes: 50 100 200 300 400 500 600 700 800 900;

/// The "accent" color suffix numbers that are available for use. This is really
/// just created for the `rmd-theme-get-swatch` mixin, but might be useful since
/// it also gets added to the scssVariables export.
/// @type List
$rmd-theme-accent-suffixes: 100 200 400 700;

